.nav {
  position: sticky;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3.125rem 0;
  border-bottom: 1px solid var(--border);
}
.nav #icon {
  width: 2.1875rem;
  cursor: pointer;
  margin: 0.625rem 0;
}
.nav .dev-name {
  color: var(--site-name);
  font-size: 1.375rem;
  cursor: pointer;
  font-weight: 700;
}
.nav__items {
  position: relative;
  display: flex;
  justify-content: center;
  gap: 1.5625rem;
  padding: 0;
}
@media (max-width: 39.9375em) {
  .nav__items {
    display: flex;
    flex-direction: column;
    position: fixed;
    left: -100%;
    top: 3.125rem;
    padding: 0.9375rem 0;
    text-align: center;
    justify-content: center;
    transition: all 0.3s linear;
    border-radius: 0.3125rem;
    box-shadow: 0px 1px 5px 1px var(--box-shadow);
  }
}
.nav__items.active {
  left: 1.25rem;
  right: 1.25rem;
  background-color: var(--app-background);
}
.nav__items .nav__link {
  position: relative;
  color: var(--text-primary);
  font-weight: 500;
  font-size: 1.125rem;
  padding-bottom: 0.3125rem;
}
.nav__items .nav__link-active {
  color: var(--active-link);
}
.nav__items .nav__marker {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 0.1875rem;
  width: 0;
  background-color: var(--active-link);
  transition: all 500ms;
  border-radius: 0.625rem;
}
@media (max-width: 39.9375em) {
  .nav__items .nav__marker {
    display: none;
  }
}
.nav .hamburger {
  display: none;
  cursor: pointer;
}
.nav .hamburger .bar {
  display: block;
  width: 1.5625rem;
  height: 0.1875rem;
  margin: 0.3125rem auto;
  transition: transform 0.3s ease-in-out;
  background-color: var(--text-primary);
}
.nav .hamburger.active .bar:nth-child(2) {
  opacity: 0;
}
.nav .hamburger.active .bar:nth-child(1) {
  transform: translateY(0.5rem) rotate(45deg);
}
.nav .hamburger.active .bar:nth-child(3) {
  transform: translateY(-0.5rem) rotate(-45deg);
}
@media (max-width: 39.9375em) {
  .nav .hamburger {
    display: block;
  }
}
.nav__last-child {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.625rem;
}

:root {
  --selected-bg: hsl(52, 100%, 50%);
  --selected-text: hsl(240, 5%, 22%);
  --app-background: hsl(0, 0%, 100%);
  --site-name: hsl(211, 100%, 50%);
  --text-primary: hsl(240, 9%, 29%);
  --active-link: hsl(167, 100%, 26%);
  --box-shadow: hsla(0, 0%, 0%, 0.2);
  --border: hsl(220, 13%, 91%);
}

body.night {
  --app-background: hsl(222, 27%, 12%);
  --text-primary: hsl(240, 11%, 72%);
  --active-link: hsl(167, 100%, 33%);
  --box-shadow: hsla(0, 0%, 100%, 0.2);
  --border: hsl(0, 0%, 38%);
}

html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

*,
:after,
:before {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

::-moz-selection {
  background: var(--selected-bg);
}

::selection {
  background: var(--selected-bg);
}

body,
html {
  margin: 0;
  padding: 0;
  background-color: var(--app-background);
  color: var(--text-primary);
  font-family: "Inter", sans-serif;
  line-height: 1.5;
  width: 100%;
  height: 100%;
}

body.night ::-moz-selection,
html.night ::-moz-selection {
  color: var(--selected-text);
  background: var(--selected-bg);
}

body.night ::selection,
html.night ::selection {
  color: var(--selected-text);
  background: var(--selected-bg);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
  outline: none;
  border: none;
}

li {
  list-style: none;
}

.container {
  width: 100%;
  padding: 0 clamp(1.375rem, 1.2rem + 0.89vw, 2rem);
  max-width: 70rem;
  margin: 0 auto;
}
@media (min-width: 70em) {
  .container {
    padding: 0;
  }
}